<template>
  <load-send-document-page v-if="hasData">
    <template v-if="!form.submitted">
      <v-form
        ref="form"
        class="form"
        v-model="form.valid"
        lazy-validation
      >
        <div class="mb-1 d-flex flex-wrap justify-space-between">
          <base-field-datepicker
            class="mb-4 full-width"
            label="Invoice Date"
            v-model="form.values.date"
            :rules="[required('Invoice Date')]"
          />
        </div>
        <div class="mb-5">
          <base-field-input
            label="Load ID"
            disabled
            background-color="grey lighten-4"
            :value="id"
            hide-details
          />

          <v-btn
            class="no-bg text-capitalize primary--text pl-0"
            depressed
            text
            :ripple="false"
            :href="`/loads/${id}`"
            target="_black"
          >
            View details
          </v-btn>
        </div>
        <div class="mb-5">
          <base-field-label-wrapper
            :text="fieldLabel"
            label-for="Email"
            class="email-label"
          >
            <template v-for="({ valueId }, index) in form.values.emails">
              <v-combobox
                :key="valueId"
                class="body-2 font-weight-bold relative"
                v-model="form.values.emails[index].value"
                dense
                outlined
                :rules="[required('Email'), email]"
                :items="contactsEmails"
                @update:search-input="(event) => getContactsEmails(event)"
                @focus="(event) => getContactsEmails('')"
              >
                <template v-slot:message="{ message }">
                  <base-field-error :message="message" />
                </template>

                <template v-slot:append-outer>
                  <v-btn
                    icon
                    width="36"
                    height="36"
                    :ripple="false"
                    @click="deleteEmail(index)"
                  >
                    <v-icon color="color-red">
                      mdi-delete-outline
                    </v-icon>
                  </v-btn>
                </template>
              </v-combobox>
            </template>
            <v-btn
              class="px-0 primary--text no-border no-bg"
              text
              depressed
              height="auto"
              :ripple="false"
              @click="addEmail"
            >
              <v-icon left>
                mdi-plus
              </v-icon>
              Add email
            </v-btn>
          </base-field-label-wrapper>

          <base-field-label-wrapper
            v-if="load.attachments.length > 0"
            class="mt-8"
            text="Attachments"
            label-for="attachments"
          >
            <p class="caption mb-2">
              Selected attachments will be included to the email message.
            </p>
            <template v-for="({ id: attachmentId, name: attachmentName, file: attachmentLink }, index) in load.attachments">
              <div
                :key="attachmentId"
                class="d-flex align-center"
              >
                <v-checkbox
                  :value="attachmentId"
                  v-model="form.values.attachments[index]"
                  class="mt-0 pa-0 text-truncate"
                  :label="attachmentName"
                  hide-details
                />

                <v-btn
                  :href="attachmentLink"
                  class="btn-no-bg text-capitalize primary--text ml-2"
                  depressed
                  text
                  :ripple="false"
                  target="_blank"
                >
                  View
                </v-btn>
              </div>
            </template>
          </base-field-label-wrapper>
        </div>
      </v-form>

      <div class="d-flex flex-wrap justify-space-between py-0 mt-5">
        <v-btn
          :href="invoiceLink"
          target="_blank"
          class="primary-text my-1"
          depressed
          :width="$vuetify.breakpoint.smAndDown ? '100%' : '100px'"
        >
          Preview
        </v-btn>

        <div class="action-btns-wrapper">
          <v-btn
            class="primary-text mr-2 my-1"
            depressed
            :width="$vuetify.breakpoint.smAndDown ? '100%' : '100px'"
            @click="$router.history.go(-1)"
          >
            Cancel
          </v-btn>

          <v-btn
            depressed
            class="my-1"
            color="primary"
            :width="$vuetify.breakpoint.smAndDown ? '100%' : '100px'"
            :disabled="!form.valid || form.values.emails.length === 0"
            :loading="form.submitting"
            @click="handleFormSubmit"
          >
            Send
          </v-btn>
        </div>
      </div>
    </template>
    <template v-else>
      <load-send-document-autoreply message="Invoice has been successfully sent." />
    </template>
  </load-send-document-page>
</template>

<script>
import { email, required } from '@utils/helpers/validationRules';
import contactsEmails from '@/mixins/contacts-emails';
import { format } from 'date-fns';

import { createNamespacedHelpers } from 'vuex';
import { actions as loadsActions, getters as loadGetters } from '@modules/loads/constants';

import LoadSendDocumentPage from '@modules/loads/components/LoadSendDocumentPage';
import LoadSendDocumentAutoreply from '@modules/loads/components/LoadSendDocumentAutoreply';
import BaseFieldLabelWrapper from '@components/BaseFieldLabelWrapper/BaseFieldLabelWrapper.vue';
import BaseFieldInput from '@components/BaseFieldInput';
import BaseFieldError from '@components/BaseFieldError';
import BaseFieldDatepicker from '@components/BaseFieldDatepicker';

const {
  mapActions: mapLoadsActions,
  mapGetters: mapLoadsGetters,
  mapState: mapLoadsState,
} = createNamespacedHelpers('loads');

export default {
  name: 'LoadSendInvoice',
  props: {
    id: {
      type: String,
      required: true,
    },
  },
  mixins: [contactsEmails],
  components: {
    LoadSendDocumentAutoreply,
    BaseFieldLabelWrapper,
    BaseFieldDatepicker,
    BaseFieldError,
    LoadSendDocumentPage,
    BaseFieldInput,
  },
  watch: {
    load: {
      handler(load) {
        if (Object.entries(load.data).length) {
          if (load.data.invoice) {
            // eslint-disable-next-line
            this.form.values.date = load.data.invoice.date.split(' ')[0];
          }
        }
      },
    },
  },
  data: () => ({
    hasData: false,
    form: {
      submitting: false,
      submitted: false,
      valid: true,
      values: {
        emails: [{
          valueId: Math.random(),
          value: '',
        }],
        attachments: [],
        date: null,
      },
    },
  }),
  methods: {
    email,
    required,
    addEmail() {
      const isAllEmailsValied = this.$refs.form.validate();
      if (isAllEmailsValied) {
        this.form.values.emails.push({
          valueId: Math.random(),
          value: '',
        });
      }
    },
    deleteEmail(index) {
      this.form.values.emails.splice(index, 1);
    },
    handleFormSubmit() {
      const isAllEmailsValied = this.$refs.form.validate();
      if (!isAllEmailsValied) return;
      const emails = this.form.values.emails.map(({ value }) => value);
      const attachments = this.form.values.attachments.filter(Boolean);

      this.form.submitting = true;
      this.sendInvoice({
        id: this.id,
        recipients: emails,
        attachments,
        billTo: this.load.data.bill_to,
        date: this.form.values.date,
      })
        .then(() => {
          this.form.submitted = true;
          this.$notificationSuccess('Invoice was successfully sent');
        }).catch(() => {
          this.$notificationError('Invoice wasn\'t successfully sent');
        }).finally(() => {
          this.form.submitting = false;
        });
    },
    ...mapLoadsActions({
      sendInvoice: loadsActions.SEND_INVOICE,
      getLoad: loadsActions.GET_LOAD,
      getLoadAttachments: loadsActions.GET_LOAD_ATTACHMENTS,
    }),
  },
  computed: {
    fieldLabel() {
      return this.form.values.emails.length > 1 ? 'Emails' : 'Email';
    },
    ...mapLoadsState(['load']),
    ...mapLoadsGetters({
      invoiceLink: loadGetters.INVOICE_LINK,
    }),
  },
  async mounted() {
    try {
      await this.getLoad({ id: this.$route.params.id });
      await this.getLoadAttachments({ id: this.$route.params.id });
      if (!this.load.data.invoice) {
        this.form.values.date = format(new Date(), 'yyyy-MM-dd');
      }

      this.form.values.emails[0].value = this.load.data.customer_email;

      this.hasData = true;
    } catch (e) {
      // TODO: log error - console.dir(e);
    }
  },
};
</script>

<style lang="scss" scoped>
  .email-label ::v-deep label {
    display: flex !important;
  }

  .action-btns-wrapper {
    @media #{map-get($display-breakpoints, 'sm-and-down')} {
      width: 100% !important;
    }
  }

  .field {
    width: 48%;

    @media #{map-get($display-breakpoints, 'sm-and-down')} {
      width: 100% !important;
    }
  }

  .no-bg::before {
    background-color: transparent !important;
  }

  ::v-deep .v-input__append-outer {
    margin-top: 0 !important;
    position: relative;
    right: 0;

    @media #{map-get($display-breakpoints, 'sm-and-down')} {
      margin-top: 5px !important;
    }
  }
</style>
